import React, { Component } from "react"
export default class Divider extends Component {
    constructor(props) {
        super(props)
    }
  render() {
      return(
        <svg throwIfNamespace version="1.1" className="divider" xmlns="http://www.w3.org/2000/svg" x="0px" y="0px"
        width="100%" viewBox="0 0 1920 80" enable-background="new 0 0 1920 80">
   <path fill={this.props.color} d="M0,0c0,0,493,80,960,80s960-80,960-80H0z"/>
   </svg>
      );
  }
}
