import React, { Component } from "react"
export default class Card extends Component {
    constructor(props) {
        super(props)
    }
  render() {
      return(
    <div className="card" style={{background: this.props.color}}>
        <div className="card-body text-light">
        <h5 className="card-title important">{this.props.title}</h5>
        <div className="card-content">
            <p className="text-light">{this.props.content}</p>
        </div>
        </div>
  </div>
      );
  }
}
export class Header extends Component {
    constructor(props) {
        super(props)
    }
    render() {
        return (
            <h1 className="text-light important" style={{marginTop: "20px", marginBottom: "20px"}}><span class="underline">{this.props.content}</span></h1>
        )
    }
}