import React, { Component } from "react"
export default class Image extends Component {
    constructor(props) {
        super(props)
    }
  render() {
      return(
    <img className="image-smooth" width="100%" src={this.props.src} />
      );
  }
}
Image.defaultProps = {
    src: "https://via.placeholder.com/1920x1080"
}